<template>
  <div id="lineDOM"></div>
</template>

<script>
import { Geometry, LineBasicMaterial, Vector3, Line } from 'three'
import ThreeBase from '@/utils/ThreeBase'
export default {
  name: 'lineDOM',
  mounted () {
    class LineClass extends ThreeBase {
      constructor () {
        super()
        document.getElementById('lineDOM').appendChild(this.renderer.domElement)
        this.geometry = new Geometry()
        this.geometry.vertices.push(new Vector3(-10, 0, 0))
        this.geometry.vertices.push(new Vector3(0, 10, 0))
        this.geometry.vertices.push(new Vector3(10, 0, 0))
        this.material = new LineBasicMaterial({ color: 0x00ff00 })
        this.line = new Line(this.geometry, this.material)
        this.scene.add(this.line)
        this.camera.position.z = 10
        this.camera.lookAt(0, 0, 0)
        this.animate = this.animate.bind(this)
      }
      animate () {
        requestAnimationFrame(this.animate)
        this.line.rotation.x += 0.01
        this.line.rotation.y += 0.01
        this.renderer.render(this.scene, this.camera)
      }
    }
    let line = new LineClass()
    line.animate()
  }

}
</script>
